/* ==========================================================================
   Footer style - begin
   ========================================================================== */

.edgtf-page-footer {
    @include edgtfRelativeHolderLayout();
    z-index: 100;
    box-sizing: border-box;

    .edgtf-footer-top-holder {
        @include edgtfRelativeHolderLayout();
        background-color: #212121;
        box-sizing: border-box;

        .edgtf-footer-top-alignment-center{
            text-align: center;
        }
	    
        .edgtf-footer-top-alignment-left{
            text-align: left;
        }
	    
        .edgtf-footer-top-alignment-right{
            text-align: right;
        }

        .edgtf-footer-top-inner{
            box-sizing: border-box;
	        
            &.edgtf-grid{
                padding: 74px 0 30px;
            }
	        
            &.edgtf-full-width{
                padding: 74px 50px 30px;
            }
        }
    }

    .edgtf-footer-bottom-holder {
        @include edgtfRelativeHolderLayout();
        background-color: #212121;
        box-sizing: border-box;
        text-align: center;

        .edgtf-footer-bottom-inner {
            box-sizing: border-box;
			border-top: 1px solid #484848;
	        
            &.edgtf-grid {
                padding: 24px 0;
            }
	        
            &.edgtf-full-width {
                padding: 24px 50px;
            }

			.widget {
				margin: 0;
			}

			.edgtf-grid-col-6 {

				&:first-child {
					text-align: left;
				}

				&:last-child {
					text-align: right;
				}
			}
        }
    }
}
/* ==========================================================================
   Footer style - end
   ========================================================================== */