/* ==========================================================================
   Blog List - Standard style - begin
   ========================================================================== */

.edgtf-blog-holder {
	
    &.edgtf-blog-standard {
	    
        article {
            margin: 0 0 50px;
            padding: 0;

            &.edgtf-post-has-media {

                .edgtf-post-heading {
                    margin: 0 0 30px;
                }
            }

            .edgtf-post-title {
				margin: 0 0 15px;
            }

			.edgtf-post-info-date {
				font-size: 12px;
				margin: 0 0 10px;
			}

            .edgtf-post-read-more-button {
                @include edgtfRelativeHolderLayout();
                margin: 7px 0 0;
            }

            .edgtf-post-info-top {
				font-size: 13px;
				margin: 0 0 5px;
	
	            > div {
		            position: relative;
		            display: inline-block;
		            padding-right: 25px;
		            margin-left: 5px;
		
		            &:after {
			            content: "/";
			            position: absolute;
			            top: 0;
			            right: 5px;
		            }
		
		            &:first-child {
			            margin-left: 0;
		            }
		
		            &:last-child {
			
			            &:after {
				            content: '';
			            }
		            }
		
		            span {
			            color: #444;
		            }
	            }
            }

			.edgtf-post-excerpt-holder {
				@include edgtfRelativeHolderLayout();
				margin: 0;
			}

            &.format-audio {

                &.edgtf-post-no-media.has-post-thumbnail {

                    .edgtf-post-heading {
                        margin: 0 0 50px;
                    }
                }
            }

            &.format-link {

				.edgtf-post-text {
					color: #fff;
					padding: 25px;
					background-color: $default-heading-color;

					.edgtf-post-text-main {
						@include edgtfTableLayout();
						text-align: center;

						.edgtf-post-mark {
							font-size: 40px;
							text-align: center;
							color: #fff;
							margin: 0 0 22px;
						}

						.edgtf-link-title {
							font-size: 23px;

							a {
								color: #fff;
							}
						}
					}
				}
            }

            &.format-quote {

				.edgtf-post-text {
					color: #fff;
					padding: 25px;
					background-color: $first-main-color;

					.edgtf-post-text-main {
						@include edgtfTableLayout();
						text-align: center;

						.edgtf-post-mark {
							font-size: 40px;
							text-align: center;
							line-height: 40px;
							color: #fff;
							margin: 0 0 4px;
						}

						.edgtf-quote-title {
							font-size: 23px;

							a {
								color: #fff;
							}
						}

						.edgtf-quote-author {
							@include edgtfRelativeHolderLayout();
							font-size: 16px;
							font-weight: 400;
							margin: 13px 0 0;
						}
					}
				}
            }
        }
    }
}
/* ==========================================================================
   Blog List - Standard style - end
   ========================================================================== */