/* ==========================================================================
   Blog Single Standard style - begin
   ========================================================================== */

.edgtf-blog-holder.edgtf-blog-single {

	&.edgtf-blog-single-standard {

		article {
			padding: 0 0 6px;
			margin: 0 0 36px;
			border-bottom: 1px solid $default-border-color;

			.edgtf-post-heading {
				margin: 0 0 27px;
			}

			.edgtf-post-title {
				font-size: 45px;
				margin: 12px 0 6px;
			}

			.edgtf-post-info-top {
				margin: 0 0 8px;

				> div {
					position: relative;
					display: inline-block;
					padding-right: 8px;
					margin-right: 3px;
					color: $default-text-color;

					a {
						color: inherit;

						&:hover {
							color: $default-heading-color;
						}
					}

					&:after {
						content: "/";
						position: absolute;
						top: 0;
						right: -2px;
					}

					&:last-child {
						padding-right: 0;
						margin-right: 0;

						&:after {
							content: '';
						}
					}
				}
			}

			.edgtf-post-info-bottom {
				@include edgtfRelativeHolderLayout();
				margin: 30px 0 0;

				.edgtf-blog-share {

					.edgtf-blog-share-label {
						@include edgtfRelativeHolderLayout();
						width: auto;
						margin: 0 7px 0 0;
						font-size: 13px;
					}

					.edgtf-list {

						li {
							margin: 0 13px 0 0;
							
							&:last-child {
								margin: 0;
							}
							
							a {
								font-size: 14px;
								color: $default-text-color;

								&:hover {
									color: $first-main-color;
								}
							}
						}
					}
				}
			}

			&.format-link {

				.edgtf-post-info-date {
					margin: 15px 0 5px;
				}

				.edgtf-post-info-bottom {
					margin:  20px 0 0;
				}

				.edgtf-post-title {
					margin: 0;
				}
			}

			&.format-quote {

				.edgtf-post-info-date {
					margin: 15px 0 5px;
				}

				.edgtf-post-info-bottom {
					margin:  20px 0 0;
				}

				.edgtf-post-title {
					margin-bottom: 0;
				}
			}

			&.format-audio {

				.edgtf-post-heading {
					position: relative;
				}

				.edgtf-blog-audio-holder {

					.mejs-audio {

						.me-cannotplay {

							span {
								padding: 5px 15px;
							}
						}
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Blog Single Standard style - end
   ========================================================================== */