.edgtf-header-vertical {

    .edgtf-content,
    footer {
        padding-left: $vertical-menu-width;
        box-sizing: border-box;
    }

    .edgtf-vertical-menu-area {
        position: fixed;
        width: $vertical-menu-width;
        top: 0;
        left: 0;
        height: 100%;
        padding: 60px 45px 10px;
        background-color: transparent;
        outline: none;
        z-index: 101;
        -webkit-backface-visibility: hidden;
        box-sizing: border-box;
    }

    .edgtf-vertical-menu-area-inner {
        @include edgtfRelativeHolderLayout();
        height: 100%;

        &.edgtf-vertical-align-left {
            text-align: left;
        }

        &.edgtf-vertical-align-center {
            text-align: center;

            .edgtf-logo-wrapper img {
                margin: 0 auto;
            }
        }
    }

    .edgtf-vertical-menu-area.edgtf-with-scroll {
        overflow: hidden;
    }

    .edgtf-vertical-area-background {
        position: fixed;
        top: 0;
        left: 0;
        width: $vertical-menu-width;
        height: 100%;
        background-color: #fff;
        background-position: right top;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 0;
        opacity: 1;
        @include edgtfTransition(opacity 0.5s ease, background-color 0.5s ease);
    }

	.edgtf-shopping-cart-dropdown {
		left: 0;
		right: auto;
	}
}