/* ==========================================================================
# Slide From Window top search general style - start
========================================================================== */

.edgtf-search-slides-from-window-top {

	.edgtf-top-bar,
	.edgtf-page-header {
		top: 0;
		@include edgtfTransition(all 0.25s ease-out);
	}

	.edgtf-mobile-header {
		@include edgtfTransition(all 0.25s ease-out);
	}

	.mobile-header-appear .edgtf-mobile-header-inner {
		padding-top: 0;
		@include edgtfTransition(all 0.25s ease-out);
	}

	&.edgtf-search-open {

		.edgtf-page-header,
		.edgtf-top-bar {
			top: 50px;
		}

		.edgtf-search-slide-window-top {
			height: 50px;
		}

		.edgtf-mobile-header {
			top: 50px !important; /* Mobile header has important top on max-width (1024px) */
		}

		.mobile-header-appear .edgtf-mobile-header-inner {
			padding-top: 50px;
		}

		.edgtf-sticky-header.header-appear {
			@include edgtfTransform(translateY(50px));
		}
	}
}

.edgtf-search-slide-window-top {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 0;
	margin: 0;
	color: #fff;
	background-color: $default-heading-color;
	overflow: hidden;
	@include edgtfTransition(all 0.25s ease-out);

	.admin-bar & {
		top: 32px;
	}

	.edgtf-fixed-wrapper.fixed & {
		position: static;
	}

	.form-inner {
		padding: 0 45px;
	}

	.edgtf-container {
		background-color: transparent;

		i {
			padding: 0;
		}

		input{
			width: 90%;
			padding: 0 0 0 10px;
		}

		.edgtf-search-close {
			right: 0;
		}
	}

	.edgtf-container-inner {
		position: relative;
	}

	i {
		color: #959595;
		font-size: 13px;
		display: inline-block;
		float: left;
		line-height: 50px;
	}

	input {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		float: left;
		width: 90%;
		height: 20px;
		margin: 15px 0;
		padding: 0 0 0 10px;
		font-family: inherit;
		font-size: 13px;
		line-height: 20px;
		color: #959595;
		background-color: transparent;
		border: none;
		border-radius: 0;
		outline: none;
		box-shadow: none;
		-webkit-appearance: none;

		&:focus{
			outline-offset: 0 !important;
		}

		@include laptop-landscape-medium {
			width: 80%;
		}

		@include ipad-portrait {
			width: 70%;
		}

		@include phone-landscape {
			width: 65%;
		}
	}

	input[type="submit"] {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		padding: 0;
		border: none;
		clip: rect(0px, 0px, 0px, 0px);
		visibility: hidden;
	}

	.edgtf-search-close {
		position: absolute;
		top: 0;
		right: 45px;
		height: 50px;
		line-height: 50px;
		color: #959595;
		font-size: 13px;
	}
}
/* ==========================================================================
# Slide From Window top search general style - end
========================================================================== */