/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.edgtf-blog-holder {
	
    &.edgtf-blog-masonry {

		&.edgtf-blog-masonry-full {
			box-sizing: border-box;

			$columns_space_label: ('tiny', 'small', 'normal');
			$columns_space: (5, 10, 15);

			@for $i from 0 to length($columns_space_label) {
				&.edgtf-blog-#{nth($columns_space_label,$i+1)}-space {
					$item_space: nth($columns_space, $i+1);

					padding: 0 #{2*$item_space}px;
				}
			}
		}

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.edgtf-post-has-media {

                .edgtf-post-heading {
                    margin: 0 0 30px;
                }
            }

			.edgtf-post-info-date {
				font-size: 12px;
				margin: 0 0 10px;
			}

            .edgtf-post-title {
                margin: 0 0 15px;
            }

			.edgtf-post-info-top {
				font-size: 13px;
				margin: 0 0 6px;

				> div {
					position: relative;
					display: inline-block;
					padding: 0 20px 0 0;
					margin: 0;

					&:after {
						content: "/";
						position: absolute;
						top: 0;
						right: 5px;
					}

					&:last-child {

						&:after {
							content: '';
						}
					}

					a {
						color: #666;

						&:hover {
							color: $first-main-color;
						}
					}

					span {
						color: #444;
					}
				}
			}

            .edgtf-post-excerpt-holder {
                @include edgtfRelativeHolderLayout();
                margin: 0;
            }

            .edgtf-post-read-more-button {
                @include edgtfRelativeHolderLayout();
                margin: 7px 0 0;
            }
        }
    }
}
/* ==========================================================================
   Blog List - Masonry style - end
   ========================================================================== */