/* Single posts navigation styles - begin
    ====================================================================== */

.edgtf-blog-single-navigation {
    @include edgtfRelativeHolderLayout();
    padding: 0;
	border-bottom: 1px solid $default-border-color;
    clear: both;
    box-sizing: border-box;

    .edgtf-blog-single-navigation-inner {
		@include edgtfTableLayout();
        padding: 0 0 36px;
    }

	.edgtf-blog-single-prev-holder,
	.edgtf-blog-single-next-holder {
		@include edgtfTableCellLayout();
		width: 50%;

		.edgtf-blog-single-thumb-wrapper,
		.edgtf-blog-single-nav-wrapper {
			@include edgtfRelativeHolderLayout();
			width: auto;
			vertical-align: bottom;
		}

		a,
		img {
			display: block;
		}

		.edgtf-blog-single-prev,
		.edgtf-blog-single-next {
			@include edgtfRelativeHolderLayout();
			color: $default-text-color;

			.edgtf-blog-single-nav-mark {
				font-size: 30px;
				line-height: inherit;
				color: inherit;

				&:before {
					display: block;
					line-height: inherit;
				}
			}

			.edgtf-blog-single-nav-label {
				color: inherit;
			}

			&:hover {
				color: $first-main-color;
			}
		}

		.edgtf-blog-single-nav-thumb {
			height: 72px;
			width: auto;

			img {
				height: 100%;
				width: auto;
			}

			&:after {
				@include edgtfAbsoluteHolderLayout();
				content: " ";
				background-color: $first-main-color;
				opacity: 0;
				@include edgtfTransition(opacity 0.34s ease-in);
			}

			&:hover {

				&:after {
					opacity: 0.4;
				}
			}
		}
	}

	.edgtf-blog-single-prev-holder {
		float: left;

		.edgtf-blog-single-prev {

			.edgtf-blog-single-nav-mark,
			.edgtf-blog-single-nav-label {
				float: left;
			}

			.edgtf-blog-single-nav-mark {
				margin: 0;
			}
		}
	}

	.edgtf-blog-single-next-holder {
		float: right;
		text-align: right;

		.edgtf-blog-single-next {

			.edgtf-blog-single-nav-mark,
			.edgtf-blog-single-nav-label {
				float: right;
			}

			.edgtf-blog-single-nav-mark {
				margin: 0;
			}
		}
	}
}

.edgtf-single-links-pages {
    clear: both;
    margin: 20px 0 5px;

    .edgtf-single-links-pages-inner {

        > a,
        > span {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 15px 5px 0;
        }
    }
}
/* Single posts navigation styles - end
    ====================================================================== */