/* ==========================================================================
   Blog List shortcode Common style - begin
   ========================================================================== */

.edgtf-blog-list-holder {
    @include edgtfRelativeHolderLayout();
    clear: both;

    .edgtf-blog-list {
        @include edgtfRelativeHolderLayout();
        list-style: none;
        margin: 0;
        padding: 0;
    }
	
	.edgtf-bl-item {
		@include edgtfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}
	
	.edgtf-bli-inner {
		@include edgtfRelativeHolderLayout();
	}
	
	.edgtf-bli-content {
		@include edgtfRelativeHolderLayout();
		box-sizing: border-box;

		.edgtf-post-info-date > a {
			color: $first-main-color;
			font-size: 12px;
			font-weight: 400;
			
			&:hover {
				color: $default-heading-color;
			}
		}

		.edgtf-bli-info {
			font-weight: 400;
			color: #333;
		}
		
		.edgtf-btn {
			color: $first-main-color;
		}
	}
	
	.edgtf-post-image {
		@include edgtfRelativeHolderLayout();
		
		a, img {
			display: block;
		}
	}
	
	.edgtf-bli-excerpt {
		@include edgtfRelativeHolderLayout();
		margin: 0;

        .edgtf-post-excerpt {
			margin: 12px 0 15px 0;

            &:after {
                content: '...';
            }
        }

		.edgtf-post-read-more-button {

			.edgtf-btn.edgtf-btn-icon>i {
				margin: 0;
			}
		}
	}

    .edgtf-blog-list-button {
        @include edgtfRelativeHolderLayout();
        margin: 0;
    }
	
	/***** Columns Space style - begin *****/
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal');
	$columns_space: (0, 5, 10, 15);
	
	@for $i from 0 to length($columns_space_label) {
		&.edgtf-bl-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space, $i+1);
			$column_width_margin: $column_width * 2;
			
			.edgtf-bl-wrapper {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}
			}
			
			.edgtf-bl-item {
				padding: 0 #{$column_width}px;
				margin: 0 0 #{$column_width_margin}px;
			}
		}
	}
	
	/***** Columns Space style - end *****/
}
/* ==========================================================================
   Blog List shortcode Common style - end
   ========================================================================== */