$logo-area-height: 100px;
$menu-area-height: 100px;
$sticky-header-height: 60px;

.edgtf-page-header {
    @include edgtfRelativeHolderLayout();
    margin: 0;
    padding: 0;
    z-index: 110;
    box-sizing: border-box;

    /* ==========================================================================
    #General header layout - start
    ========================================================================== */
    a img {
        display: block;
    }

    p {
        margin: 0;
    }
    
    .edgtf-vertical-align-containers {
        padding: 0 60px;
    }

    .edgtf-grid {
        height: 100%;

        .edgtf-vertical-align-containers {
            padding: 0;
            @include edgtfTransition(background-color 0.3s ease);
        }
    }

    /* ==========================================================================
    #General header layout - end
    ========================================================================== */

    /* ==========================================================================
    #Logo area styles - start
    ========================================================================== */
    .edgtf-logo-area {
        height: $logo-area-height;
        overflow: hidden;
        position: relative;
        background-color: #fff;

        .edgtf-logo-wrapper a {
            max-height: $logo-area-height;
        }
    }
    /* ==========================================================================
    #Logo area styles - end
    ========================================================================== */

    /* ==========================================================================
    #Menu area styles - start
    ========================================================================== */
    .edgtf-menu-area {
        position: relative;
        height: $menu-area-height;
        box-sizing: border-box;

        .edgtf-boxed & {
            padding: 0;
        }

        .edgtf-logo-wrapper a {
            max-height: $menu-area-height;
        }

        .widget {
            display: inline-block;
            vertical-align: middle;
            margin: 0;
        }
    }
    /* ==========================================================================
    #Menu area styles - end
    ========================================================================== */

    /* ==========================================================================
    #Sticky header styles - start
    ========================================================================== */
    .edgtf-sticky-header {
        height: $sticky-header-height;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
        @include edgtfTransform(translateY(-100%));
        -webkit-transition: -webkit-transform 0.6s ease;
        -moz-transition: -moz-transform 0.6s ease;
        transition: transform .6s ease;
        -webkit-backface-visibility: hidden;

        .edgtf-grid {
            height: 100% !important; //override settings for other instances of this div
        }

        &.header-appear {
            transform: translateY(0%);
        }

        .edgtf-sticky-holder {
            height: 100%;
            position: relative;
            z-index: 2;
            background-color: #fff;
            border-bottom: 1px solid $default-border-color;
            box-sizing: border-box;

            .edgtf-vertical-align-containers {
                padding: 0 40px;
            }
        }

        .admin-bar &.header-appear {
            top: 32px;
        }

        .edgtf-sticky-holder .edgtf-logo-wrapper a {
            max-height: $sticky-header-height;
        }

        .edgtf-position-center-inner {
            height: 100%;
        }

        .edgtf-sticky-right {
            display: inline-block;
            vertical-align: middle;
        }
    
        .widget {
            display: inline-block;
            vertical-align: middle;
            margin: 0;
        }
    }
    /* ==========================================================================
    #Sticky header styles - end
    ========================================================================== */

    /* ==========================================================================
    #Fixed header styles - start
    ========================================================================== */
    .edgtf-fixed-wrapper {

		.edgtf-menu-area {
			border-bottom: 0;
			@include edgtfTransition(height .2s ease-in-out);
		}

		&.fixed {
			position: fixed;
			width: 100%;
			left: 0;
			top: 0;
			background-color: #fff;
			border-bottom: 1px solid transparent;
			box-shadow: 0 5px 5px 0 rgba($default-heading-color, .05);
			-webkit-backface-visibility: hidden;

			.admin-bar & {
				margin-top: 32px;
			}
		}
    }

    /* ==========================================================================
    #Fixed header - end
    ========================================================================== */
}

.edgtf-header-logo-area-in-grid-padding .edgtf-page-header .edgtf-logo-area .edgtf-vertical-align-containers,
.edgtf-header-menu-area-in-grid-padding .edgtf-page-header .edgtf-menu-area .edgtf-vertical-align-containers {
    padding: 0px 25px;
}

.edgtf-menu-area-shadow-disable .edgtf-page-header .edgtf-menu-area,
.edgtf-menu-area-in-grid-shadow-disable .edgtf-page-header .edgtf-menu-area .edgtf-grid .edgtf-vertical-align-containers{
    box-shadow: none;
}

.edgtf-logo-area-border-disable .edgtf-page-header .edgtf-logo-area,
.edgtf-logo-area-in-grid-border-disable .edgtf-page-header .edgtf-logo-area .edgtf-grid .edgtf-vertical-align-containers,
.edgtf-menu-area-border-disable .edgtf-page-header .edgtf-menu-area,
.edgtf-menu-area-in-grid-border-disable .edgtf-page-header .edgtf-menu-area .edgtf-grid .edgtf-vertical-align-containers{
    border: none;
}

.edgtf-header-vertical-shadow-disable .edgtf-vertical-area-background{
    box-shadow: none;
}

.edgtf-header-vertical-border-disable .edgtf-vertical-area-background{
    border: none;
}

/* ==========================================================================
#Light header - begin
========================================================================== */

.edgtf-light-header {

    .edgtf-page-header > div:not(.fixed):not(.edgtf-sticky-header) {

        .edgtf-logo-wrapper {

            .edgtf-light-logo {
                opacity: 1;
            }

            .edgtf-normal-logo,
            .edgtf-dark-logo {
                opacity: 0;
            }
        }

        .edgtf-menu-area {
            color: $header-light-color !important; //Beacause of inline color styles

            .widget {
                color: $header-light-color !important; //Beacause of inline color styles

                p,
                a:not(.edgtf-btn),
                span:not(.edgtf-btn-text),
                i,
                .widgettitle {
                    color: $header-light-color !important; //Beacause of inline color styles
                }

                a:not(.edgtf-btn):hover {
                    color: $header-light-hover-color !important; //Beacause of inline color styles
                }
                
                a.edgtf-btn:not(:hover) {
	                color: $header-light-color !important; //Beacause of inline color styles
                }
            }
        }

        &.edgtf-menu-area {
            color: $header-light-color !important; //Beacause of inline color styles

            .widget {
                color: $header-light-color !important; //Beacause of inline color styles

                p,
                a:not(.edgtf-btn),
                span:not(.edgtf-btn-text),
                i,
                .widgettitle {
                    color: $header-light-color !important; //Beacause of inline color styles
                }

                a:not(.edgtf-btn):hover {
                    color: $header-light-hover-color !important; //Beacause of inline color styles
                }
	
	            a.edgtf-btn:not(:hover) {
		            color: $header-light-color !important; //Beacause of inline color styles
	            }
            }
        }
    }

    .edgtf-vertical-menu-area {

        .edgtf-logo-wrapper {

            .edgtf-light-logo {
                opacity: 1;
            }

            .edgtf-normal-logo,
            .edgtf-dark-logo {
                opacity: 0;
            }
        }
    }
}

/* ==========================================================================
#Light header - end
========================================================================== */

/* ==========================================================================
#Dark header - begin
========================================================================== */

.edgtf-dark-header {

    .edgtf-page-header {

        > div:not(.fixed):not(.edgtf-sticky-header) {

            .edgtf-logo-wrapper {

                .edgtf-dark-logo {
                    opacity: 1;
                }

                .edgtf-normal-logo,
                .edgtf-light-logo {
                    opacity: 0;
                }
            }

            .edgtf-menu-area {
                color: $header-dark-color !important; //Because of inline color styles

                .widget {
                    color: $header-dark-color !important; //Beacause of inline color styles

                    p,
                    a:not(.edgtf-btn),
                    span:not(.edgtf-btn-text),
                    i,
                    .widgettitle {
                        color: $header-dark-color !important; //Beacause of inline color styles
                    }

                    a:not(.edgtf-btn):hover {
                        color: $header-dark-hover-color !important; //Beacause of inline color styles
                    }
                }
            }

            &.edgtf-menu-area {
                color: $header-dark-color !important; //Because of inline color styles

                .widget {
                    color: $header-dark-color !important; //Beacause of inline color styles

                    p,
                    a:not(.edgtf-btn),
                    span:not(.edgtf-btn-text),
                    i,
                    .widgettitle {
                        color: $header-dark-color !important; //Beacause of inline color styles
                    }

                    a:not(.edgtf-btn):hover {
                        color: $header-dark-hover-color !important; //Beacause of inline color styles
                    }
                }
            }
        }
    }

    .edgtf-vertical-menu-area {

        .edgtf-logo-wrapper {

            .edgtf-dark-logo {
                opacity: 1;
            }

            .edgtf-normal-logo,
            .edgtf-light-logo {
                opacity: 0;
            }
        }
    }
}

/* ==========================================================================
#Dark header - end
========================================================================== */