$top-bar-height: 44px;

.edgtf-top-bar-background {
	position: absolute;
	height: 100px;
	width: 100%;
	left: 0;
	top: 0;
	background-color: #181818;
	z-index: 101;
}

.edgtf-top-bar {
	@include edgtfRelativeHolderLayout();
	height: $top-bar-height;
	color: #ccc;
	font-size: 11px;
	background-color: #181818;
	z-index: 111;
	box-sizing: border-box;
	
	> .edgtf-vertical-align-containers {
		padding: 0 60px;
	}
	
	.edgtf-grid {
		height: 100%;

		.edgtf-vertical-align-containers {
			padding-left: 0;
			padding-right: 0;

			.edgtf-top-bar-in-grid-padding & {
				padding: 0 60px;
			}
		}
	}

	.widget {
		margin: 0;
	}
}

.edgtf-top-bar-light .edgtf-top-bar {
	color: $header-light-color; //Beacause of inline color styles

	.widget:not(.widget_edgtf_woocommerce_dropdown_cart) {
		color: $header-light-color; //Beacause of inline color styles

		p,
		a,
		span,
		i,
		.widgettitle {
			color: $header-light-color !important; //Beacause of inline color styles
		}

	}

	.edgtf-shopping-cart-holder .edgtf-header-cart {
		.edgtf-cart-icon {
			color: #fff;
		}

		.edgtf-cart-number {
			background-color: #fff;
			color: #9e9e9e;
		}
	}
}

.edgtf-top-bar-dark .edgtf-top-bar {
	color: $header-dark-color; //Beacause of inline color styles

	.widget {
		color: $header-dark-color; //Beacause of inline color styles

		p,
		a,
		span,
		i,
		.widgettitle {
			color: $header-dark-color !important; //Beacause of inline color styles
		}

	}
}

.edgtf-top-bar-gray .edgtf-top-bar {
	color: #777777; //Beacause of inline color styles

	.widget {
		color: #777777; //Beacause of inline color styles

		p,
		a,
		span,
		i,
		.widgettitle {
			color: #777777; //Beacause of inline color styles
		}

	}
}