/* Single author box styles - begin
	====================================================================== */

.edgtf-author-description {
	@include edgtfRelativeHolderLayout();
	background-color: #f7f7f7;
	margin: 0 0 37px;

	.edgtf-author-description-inner {
		@include edgtfRelativeHolderLayout();
		min-height: 142px;
		padding: 24px 28px;
		box-sizing: border-box;
	}

	.edgtf-author-description-image {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		float: left;
		border-radius: 100%;
		overflow: hidden;

		img {
			display: block;
		}
	}

	.edgtf-author-description-text-holder {
		position: relative;
		padding: 0 0 0 162px;

		.edgtf-author-name {
			margin: 14px 0 0;
		}

		.edgtf-author-email {
			margin: 0;
		}

		.edgtf-author-text {
			@include edgtfRelativeHolderLayout();

			> p {
				margin: 9px 0 0;
			}
		}

		.edgtf-author-social-icons {
			@include edgtfRelativeHolderLayout();
			margin: 13px 0 0;

			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				padding: 0;
				margin: 0 10px 0 0;
				font-size: 12px;

				&:last-child {
					margin: 0;
				}
			}
		}
	}

	@include ipad-portrait {
		.edgtf-author-description-content {
			padding: 0;
		}
		.edgtf-author-description-image {
			display: block;
			width: 100%;
		}
		.edgtf-author-description-text-holder {
			display: block;
			padding: 0;
			margin-top: 30px;
		}
	}
}

/* Single author box styles - end
	====================================================================== */