/* ==========================================================================
   Blog Common style - begin
   ========================================================================== */

.edgtf-blog-holder {
    @include edgtfRelativeHolderLayout();
    clear: both;

    article {
        @include edgtfRelativeHolderLayout();
        box-sizing: border-box;

        &.sticky {

            .edgtf-post-title a {
                color: $first-main-color;
            }
        }

        .edgtf-post-content {
            @include edgtfRelativeHolderLayout();
        }

        .edgtf-post-image {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            max-width: 100%;

            img {
                display: block;
            }
        }

        .edgtf-post-text {
            @include edgtfRelativeHolderLayout();
            box-sizing: border-box;
        }

        .edgtf-post-excerpt {

            &:after {
                content: '...';
            }
        }

        .edgtf-post-info {
            @include edgtfRelativeHolderLayout();

            > * {
                position: relative;
                display: inline-block;
                vertical-align: top;
                color: currentColor;

                a {
                    color: inherit;
                }
            }
        }

		&.format-link {

			.edgtf-post-text {
				color: #fff;
				padding: 43px 35px;
				background-color: $default-heading-color;

				.edgtf-post-text-main {
					@include edgtfTableLayout();
					text-align: center;

					.edgtf-post-mark {
						font-size: 40px;
						text-align: center;
						color: #fff;
						margin: 0 0 22px;
					}

					.edgtf-link-title {
						font-size: 23px;

						a {
							color: #fff;
						}
					}
				}
			}
		}

		&.format-quote {

			.edgtf-post-text {
				color: #fff;
				padding: 42px 35px 45px;
				background-color: $first-main-color;

				.edgtf-post-text-main {
					@include edgtfTableLayout();
					text-align: center;

					.edgtf-post-mark {
						font-size: 40px;
						color: #fff;
						text-align: center;
						margin: 0 0 16px;
					}

					.edgtf-quote-title {
						font-size: 23px;
						line-height: 40px;
						color: #fff;
						margin: 0 0 4px;

						a {
							color: #fff;
						}
					}

					.edgtf-quote-author {
						@include edgtfRelativeHolderLayout();
						font-size: 16px;
						font-weight: 400;
						margin: 12px 0 0;
					}
				}
			}
		}

        //Gallery post style
        &.format-gallery {

            .edgtf-post-image {
                width: 100%;
            }

            .owl-carousel .owl-item {
                z-index: 2;

                &.active {
                    z-index: 3;
                }
            }
        }

        //Audio post style
        &.format-audio {

			.edgtf-post-heading {
				position: relative;
				margin: 30px 0 0;
			}

            .edgtf-blog-audio-holder {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;

                .mejs-container, .mejs-embed, .mejs-embed body, .mejs-container .mejs-controls {
                    background: none;
                }

                .mejs-container {
                    background-color: rgba(0,0,0,0.8);
                }

                .mejs-container,
                .mejs-container .mejs-controls {
                    height: 38px !important;
                }

                .mejs-container .mejs-controls {
                    padding: 0 10px 0 4px;
                    box-sizing: border-box;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button {
                    width: 39px;
                }

                .mejs-container .mejs-controls > .mejs-volume-button {
                    width: 30px;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button,
                .mejs-container .mejs-controls > .mejs-volume-button {
                    height: 38px;

                    button {
                        position: relative;
                        height: 100%;
                        background: none;
                        background-position: center;
                        background-repeat: no-repeat;
                        margin: 0;
                        border: 0;
                        outline: none;
                    }

                    &.mejs-play button {
                        width: 39px;
                        background-image: url('../img/audio/play.png');
                    }

                    &.mejs-pause button {
                        width: 39px;
                        background-image: url('../img/audio/pause.png');
                    }

                    &.mejs-stop button {
                        width: 39px;
                        background-image: url('../img/audio/stop.png');
                    }

                    &.mejs-mute button {
                        width: 30px;
                        background-image: url('../img/audio/sound.png');
                    }

                    &.mejs-unmute button {
                        width: 30px;
                        background-image: url('../img/audio/mute.png');
                    }
                }

                .mejs-container .mejs-controls > .mejs-time {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;
                    font-size: 13px;
                    color: #fff;
                    line-height: 36px;
                }

                .mejs-container .mejs-controls > .mejs-time-rail {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;

                    .mejs-time-total {
                        height: 5px;
                        margin: 15px 14px;
                        background: none;
                        background-color: #fff;

                        .mejs-time-current {
                            height: 5px;
                            background-color: $first-main-color;
                        }

                        .mejs-time-float {
                            top: -14px;
                            border: 0;
                            color: #fff;

                            .mejs-time-float-corner {
                                border-top-color: $default-heading-color;
                            }
                        }

                        .mejs-time-loaded,
                        .mejs-time-float {
                            background: none;
                        }
                    }
                }

                .mejs-container .mejs-controls > a.mejs-horizontal-volume-slider {
                    height: 38px;

                    .mejs-horizontal-volume-total,
                    .mejs-horizontal-volume-current {
                        height: 5px;
                        top: 15px;
                        background: none;
                    }

                    .mejs-horizontal-volume-total {
                        background-color: #fff;
                    }

                    .mejs-horizontal-volume-current {
                        background-color: $first-main-color;
                    }
                }
            }
        }

		&.format-video {

			.edgtf-self-hosted-video {
				max-width: 100%;
			}
		}
    }
}

/* ==========================================================================
   Blog Common style - end
   ========================================================================== */