/* ==========================================================================
   Blog Slider shortcode style - begin
   ========================================================================== */

.edgtf-blog-slider-holder {

    .edgtf-blog-slider-item {
        @include edgtfRelativeHolderLayout();
        list-style-type: none;
        text-align: center;

        .edgtf-item-image {
            @include edgtfRelativeHolderLayout();

            &::after {
                @include edgtfAbsoluteHolderLayout();
                content: "";
                background: rgba($default-heading-color, 0.3);
            }
        }

        .edgtf-item-text-wrapper {
            @include edgtfAbsoluteHolderLayout();
            opacity: 0;
            @include edgtfTransition(opacity 0.45s ease-out);
        }

        .edgtf-item-text-holder {
            @include edgtfTableLayout();
        }
    
        .edgtf-item-text-holder-inner {
            @include edgtfTableCellLayout();
            padding: 0 5%;
        }
    
        .edgtf-item-info-section{
            margin-bottom: 5px;
    
            > div {
                position: relative;
                display: inline-block;
				color: #fff;
				padding-right: 25px;
                margin-left: 5px;
        
                &:after {
                    content: "/";
                    position: absolute;
                    top: 0;
                    right: 5px;
                }
        
                &:first-child {
                    margin-left: 0;
                }
        
                &:last-child {
            
                    &:after {
                        content: '';
                    }
                }

				span, a{
					color: #fff;
				}

				a {

					&:hover {
						color: $first-main-color;
					}
				}
            }
        }
    
        .edgtf-item-title {
            margin: 0 0 15px;
            color: #fff;
        }

        .edgtf-section-button-holder {
            @include edgtfRelativeHolderLayout();
            margin: 20px 0 0;

            a {
                color: #fff;

                &:hover {
                    color: $first-main-color !important;
                }
            }
        }
    }

    .active.center {
	    
        .edgtf-blog-slider-item {
	        
            .edgtf-item-text-wrapper {
                opacity: 1;
            }
        }
    }
}
/* ==========================================================================
   Blog Slider shortcode style - end
   ========================================================================== */