/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.edgtf-sidebar {
	position: relative;
	z-index: 11;
	margin: 0 0 60px;
	
	.widget {
		
		&:last-child {
			margin: 0 !important;
		}
	}
}

.wpb_widgetised_column {
	position: relative;
	
	.widget {
		
		.edgtf-sidearea-widget-title {
			margin: 0 0 23px;
		}
		
		&:last-child {
			margin: 0 !important;
		}
	}
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 30px;
	
	.edgtf-widget-title {
		margin: 0 0 15px;
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}
	
	/*
	*   Specific styles
	*/
	&.widget_text {
		
		img {
			display: block;
		}
	}
	
	#wp-calendar {
		width: 100%;
		text-align: center;
		
		caption {
			color: $default-heading-color;
			margin: 0 0 15px;
		}
		
		th, td {
			padding: 3px 4px;
		}
		
		td#today {
			color: #fff;
			background-color: $default-heading-color;
			
			a {
				color: inherit;
			}
		}
	}
	
	&.widget_rss {
		
		.edgtf-widget-title {
			
			.rsswidget {
				color: inherit;
			}
		}
		
		.rss-widget-icon {
			display: inline-block;
		}
		
		ul {
			
			li {
				margin: 0 0 25px;
				
				.rss-date {
					display: block;
				}
			}
		}
	}
	
	&.widget_search {
		
		input,
		button {
			position: relative;
			display: inline-block;
			vertical-align: top;
			padding: 0;
			margin: 0;
			float: left;
			height: 44px;
			font-family: inherit;
			font-size: 13px;
			font-weight: inherit;
			line-height: 44px;
			color: #666;
			background-color: transparent;
			border-radius: 0;
			outline: 0;
			-webkit-appearance: none;
			box-sizing: border-box;
		}
		
		input {
			width: calc(100% - 44px);
			padding: 0 10px 0 20px;
			border: 1px solid $default-border-color;
			border-right: 0;
			
			@include edgtfPlaceholder {
				color: #666;
			}
		}
		
		button {
			width: 44px;
			font-size: 12px;
			font-weight: 400;
			letter-spacing: 1px;
			color: #fff;
			background-color: $default-heading-color;
			border: 0;
			text-align: center;
			cursor: pointer;
			@include edgtfTransition(background-color .2s ease-in-out);
			
			&:hover {
				background-color: $first-main-color;
			}
		}
	}
	
	&.widget_pages,
	&.widget_archive,
	&.widget_categories,
	&.widget_meta,
	&.widget_recent_comments,
	&.widget_recent_entries,
	&.widget_nav_menu {
		
		ul {
			
			li {
				margin: 0;
				padding: 7px 0;
				
				ul {
					padding: 0 0 0 10px;
					
					li {
						
						&:last-child {
							padding: 7px 0 0;
						}
					}
				}
			}
		}
	}
	
	&.widget_recent_entries {
		
		ul {
			
			li {
				
				> span {
					display: block;
				}
			}
		}
	}
	
	&.widget_archive,
	&.widget_categories,
	&.widget_text {
		
		select {
			@include edgtfRelativeHolderLayout();
			max-width: 100%;
			padding: 12px;
			font-family: inherit;
			font-size: inherit;
			line-height: inherit;
			font-weight: inherit;
			color: #666;
			background-color: transparent;
			border: 1px solid $default-border-color;
			border-radius: 0;
			outline: 0;
			box-sizing: border-box;
		}
	}
	
	&.posts_holder {
		
		ul {
			
			li {
				
				&.page_item_has_children {
					
					& > a {
						display: block;
					}
					
					& > ul.children {
						padding-left: 10px;
					}
				}
				
				&.menu-item-has-children {
					
					& > a {
						display: block;
					}
					
					& > ul.sub-menu {
						padding-left: 10px;
					}
				}
			}
		}
	}
	
	&.widget_tag_cloud {
		
		.edgtf-widget-title {
			margin: 0 0 32px;
		}
		
		a {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			margin: 0 3px 7px 0;
			padding: 0 22px;
			color: $default-heading-color;
			font-family: inherit;
			font-size: 10px !important;
			line-height: 39px;
			font-weight: 500;
			letter-spacing: 2px;
			text-transform: uppercase;
			background-color: #f4f4f4;
			@include edgtfTransition(color .2s ease-out, background-color .2s ease-out);
			
			&:hover {
				color: #fff;
				background-color: $first-main-color;
			}
		}
	}
	
	&.edgtf-blog-list-widget {
		margin: 0;
		
		.edgtf-widget-title {
			margin: 0 0 24px;
		}
	}
	
	&.edgtf-image-widget {
		margin: 0;
		
		.edgtf-widget-title {
			margin: 0 0 28px;
		}
		
		img {
			display: block;
		}
		
		.edgtf-drop-down .second .inner & {
			
			a {
				padding-top: 0;
			}
		}
	}
	
	&.edgtf-image-slider-widget {
		@include edgtfRelativeHolderLayout();
		
		.edgtf-is-widget-inner {
			@include edgtfRelativeHolderLayout();
			visibility: hidden;
		}
		
		.edgtf-widget-title {
			margin: 0 0 28px;
		}
		
		img {
			display: block;
		}
		
		.owl-dots {
			text-align: center;
			margin: 19px 0 0;
			
			.owl-dot {
				display: inline-block;
				vertical-align: middle;
				
				span {
					display: inline-block;
					vertical-align: middle;
					width: 10px;
					height: 10px;
					@include edgtfTransform(scale(.8));
					margin: 0 6px;
					border-radius: 3em;
					background-color: rgba($default-heading-color, 0.3);
					box-sizing: border-box;
					@include edgtfTransition(all .2s);
				}
				
				&.active {
					
					span {
						@include edgtfTransform(scale(1));
						background-color: $default-heading-color;
					}
				}
			}
		}
		
		.owl-carousel .owl-item img {
			-webkit-transform: translateZ(0); //chrome text cut off fix
		}
	}
	
	&.edgtf-raw-html-widget {
		margin: 0;
		
		&.edgtf-grid-auto-width {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: auto;
		}
	}
	
	&.edgtf-separator-widget {
		margin: 0 !important;
	}
}

aside.edgtf-sidebar,
.wpb_widgetised_column {
	
	.widget {
		
		h4.edgtf-widget-title {
			font-weight: 500;
		}
		
		&.widget_pages,
		&.widget_archive,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_comments,
		&.widget_recent_entries,
		&.widget_nav_menu {
			
			.edgtf-widget-title {
				margin: 0 0 15px;
			}
			
			ul {
				padding: 0;
				margin: 0;
				list-style: none;
				
				li {
					@include edgtfRelativeHolderLayout();
					padding: 0;
					margin: 0 0 4px;
					clear: both;
					
					&:last-child {
						margin: 0;
					}
					
					a {
						color: $default-text-color;
						
						&:hover {
							color: $first-main-color;
						}
					}
					
					ul {
						margin: 4px 0 0 10px;
					}
				}
			}
		}
		
		&.widget_pages,
		&.widget_archive,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_entries,
		&.widget_nav_menu {
			
			ul {
				
				li {
					
					a {
						box-sizing: border-box;
						@include edgtfTransition(color .3s ease-in-out, padding .3s ease-in-out);
						
						&:hover {
							padding: 0 0 0 10px;
						}
					}
				}
			}
		}
	}
}

