footer .edgtf-footer-bottom-holder {
	
	.widget {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		font-size: 12px;
		
		p {
			margin: 0;
		}
		
		&.widget_pages,
		&.widget_archive,
		&.widget_categories,
		&.widget_recent_comments,
		&.widget_recent_entries,
		&.widget_nav_menu  {
			
			ul {
				clear: both;
				
				li {
					float: left;
					padding: 0 8px 0 0;
					margin: 0;
					
					&:last-child {
						padding: 0;
					}
				}
			}
		}
	}
}

.edgtf-top-bar {
	
	.widget {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		
		p {
			margin: 0;
		}
		
		a {
			color: #fff;
			
			&:hover {
				color: $first-main-color;
			}
		}
		
		&.widget_nav_menu  {
			
			ul {
				clear: both;
				
				li {
					position: relative;
					float: left;
					padding: 0 13px 0 0;
					margin: 0 13px 0 0;
					
					&:after {
						content: '';
						position: absolute;
						top: calc(50% - 5px);
						right: 0;
						width: 1px;
						height: 10px;
						display: block;
						background-color: #666;
					}
					
					&:last-child {
						padding: 0;
						margin: 0;
						
						&:after {
							display: none;
						}
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */