.edgtf-main-menu {
    @include edgtfRelativeHolderLayout();
    height: 100%;
    width: auto;

    ul {
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            float: left;
            padding: 0;
            margin: 0;

            a {
                font-size: 13px;
                line-height: 24px;
				font-weight: 300;
                color: $default-text-color;

                &:hover {
                    color: $first-main-color;
                }

                i:before {
                    margin: 0 7px 0 0;
                }

                .menu_icon_wrapper {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                }

                .edgtf-menu-featured-icon {
                    position: relative;
                    top: -4px;
                    left: 5px;
                    color: currentColor;
                    font-size: 10px;
                    margin: 0;
	                
	                &.edgtf-new-text {
                        color: $first-main-color;
		                font-family: $default-text-font;
		                font-size: 11px;
		                font-weight: 400;
		                
		                &:before {
			                content: 'New';
		                }
	                }

                    &:before {
                        margin: 0;
                    }
                }
            }
        }
    }

    > ul {
        @include edgtfRelativeHolderLayout();
        height: 100%;

        > li {
            float: left;
            height: 100%;
            box-sizing: border-box;

            > a {
                @include edgtfRelativeHolderLayout();
                height: 100%;
                padding: 0 15px;
                color: $default-text-color;
                font-size: 13px;
	            line-height: 23px;
	            font-weight: 400;
	            letter-spacing: 0.5px;
                box-sizing: border-box;

                > span.item_outer {
                    position: relative;
                    display: block;
                    top: 50%;
                    @include edgtfTransform(translateY(-50%));

					.item_text {
						position: relative;
						display: inline-block;
						vertical-align: middle;
						padding: 0 2px;
						line-height: 26px; //blur fix

						&:after {
							content: '';
							position: absolute;
							left: 0;
							bottom: 1px;
							width: 100%;
							height: 2px;
							background-color: $first-main-color;
							opacity:0;
							@include edgtfTransform(scaleY(0) translateY(-5px));
							-webkit-transform-origin: 50% 0;
							transform-origin: 50% 0;
							@include edgtfTransition(all .3s);
						}
					}
                }
            }

			&:last-child {

				> a {
					padding: 0 30px 0 15px;
				}
			}

			&.edgtf-active-item > a {
				color: $default-text-color;

				> span.item_outer .item_text {

					&:after {
						opacity:1;
						@include edgtfTransform(scaleY(1) translateY(0));
					}
				}
			}

			&:hover {

				> a {
					color: $default-text-color;

					> span.item_outer .item_text {

						&:after {
							opacity: 1;
							@include edgtfTransform(scaleY(1) translateY(0));
						}
					}
				}
			}

            &.has_sub {

                > a .edgtf-menu-arrow {
                    display: none;
                }
            }
        }
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) {

    .edgtf-main-menu > ul > li {

        > a {
            color: $header-light-color;

            &:hover {
                color: $header-light-hover-color;
            }
        }

        &.edgtf-active-item > a {
            color: $header-light-hover-color;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) {

    .edgtf-main-menu > ul > li {

        > a {
            color: $header-dark-color;

            &:hover {
                color: $header-dark-color;
            }
        }

        &.edgtf-active-item > a {
            color: $header-dark-color;
        }
    }
}

/* Narrow drop down styles
   ========================================================================== */
.edgtf-drop-down {

    .second {
        position: absolute;
        display: block;
        left: -13px;
        top: 100%;
        visibility: hidden;
        overflow: hidden;
        opacity: 0;
        z-index: 10;
        @include edgtfTransition(top 0.15s ease-out);

        &.edgtf-drop-down-start {
            visibility: visible;
            overflow: visible;
            opacity: 1;
            @include edgtfTransition(opacity 0.2s ease-out);
        }

        &.right {
            right: -13px;
            left: auto;
        }
    }

    .second .inner {
        position: relative;
        display: block;
        padding: 0;
        margin: 0;
        z-index: 997;
        box-shadow: 0 5px 5px 0 rgba(0,0,0,0.03);

        ul {

            li {
                display: block;
                float: none;
                height: auto;

                .widget {
                    width: 100%;
                    margin: 0 !important;
                }

                a {
                    display: block;
                    float: none;
                    margin: 0;
                    padding: 5px 30px;

                    .item_outer {
                        position: relative;
                        display: block;
                        overflow: hidden;
                        padding: 0 20px 0 0;
                        box-sizing: border-box;
                        @include edgtfTransition(padding .2s ease-in-out);
                    }

                    &:hover {

                        .item_outer {
                            padding: 0 10px;

                            &:before {
                                left: 0;
                            }
                        }
                    }
                }

                &.current-menu-ancestor > a,
                &.current-menu-item > a {
                    color: $first-main-color;

                    .item_outer {
                        padding: 0 10px;

                        &:before {
                            left: 0;
                        }
                    }
                }

                &.sub {

                    > a {

                        .item_outer {

                            &:after {
                                position: absolute;
                                display: inline-block;
                                vertical-align: middle;
                                top: -2px;
                                right: -2px;
                                content: '\e04b';
                                font-family: 'linea-arrows-10';
                                font-size: 17px;
                                font-weight: normal;
                                font-style: normal;
                                text-rendering: auto;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }
                        }
                    }
                }
            }

            &.right {

                li {

                    a {

                        .item_outer {
                            padding: 0 0 0 20px;
                        }

                        &:hover {

                            .item_outer {
                                padding: 0 10px;

                                &:before {
                                    left: auto;
                                    right: 0;
                                }
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {

                        .item_outer {
                            padding: 0 10px;

                            &:before {
                                left: auto;
                                right: 0;
                            }
                        }
                    }

                    &.sub {

                        > a {

                            .item_outer {

                                &:after {
                                    right: auto;
                                    left: -2px;
                                    content: "\e03f";
                                }
                            }
                        }
                    }

                    a {
                        text-align: right;
                    }

                    ul {
                        left: -100%;
                    }
                }
            }
        }
    }

    li.left_position .second {
        left: -13px;
    }

    li.right_position .second {
        left: auto;
        right: -13px;
    }

    > ul > li {
        &:hover > .second {
            z-index: 20;
        }
    }

    .narrow .second .inner {

        ul {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            background-color: #fff;
            padding: 23px 0;

            li {
                width: 242px;

                ul {
                    position: absolute;
                    left: 100%;
                    top: -23px;
                    height: auto;
                    opacity: 0;
                    overflow: hidden;
                    z-index: 10;
                    box-shadow: 0 5px 5px 0 rgba(0,0,0,0.03);
                    -webkit-transform: translateZ(0);
                    @include edgtfTransition(opacity .4s ease-out);
                }

                &:hover ul {
                    z-index: 20;
                    opacity: 1;
                }
            }

            &.right li ul {
                left: -100%;
            }
        }
    }
}

/* Wide drop down styles
   ========================================================================== */
.edgtf-drop-down .wide {

    .second {
        left: 0;
        right: auto;
        
        .inner {
            background-color: #fff;
            text-align: center;

            ul {

                li {

                    a {
                        padding: 7px 0;
                    }

                    ul {
                        position: relative;
                        display: block;
                        top: 0;
                        left: 0;
                        visibility: visible;

                        li {

                            &:last-child {

                                a {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }

            > ul {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                width: 1100px;
                padding: 35px 0;
                box-sizing: border-box;

                .edgtf-grid-1300 &{
                    width: $grid-width-1300;
                }

                .edgtf-grid-1200 &{
                    width: $grid-width-1200;
                }

                .edgtf-grid-1000 &{
                    width: $grid-width-1000;
                }

                .edgtf-grid-800 &{
                    width: $grid-width-800;
                }

                > li {
                    float: left;
                    width: 25%;
                    margin: 0;
                    padding: 0 26px;
                    border-left: 1px solid $default-border-color;
                    box-sizing: border-box;

                    &:nth-child(4n+1) {
                        clear: both;
                    }

                    > a {
                        padding: 0 0 17px;
                        color: $default-heading-color;
                        font-size: 16px;
						font-weight: 500;
                        line-height: 1.1em;

                        &.no_link {
                            cursor: default;
                        }

                        .item_outer {
                            padding: 0;
                            overflow: inherit;

                            &:before,
                            &:after {
                                display: none;
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }

    &.left_position,
    &.right_position {

        .second .inner > ul {
            width: auto;
            white-space: nowrap;

            > li {
                float: none;
                display: inline-block;
                vertical-align: top;

                &:nth-child(4n+1) {
                    border-left: none;
                }
            }
        }

        .second ul li {
            width: 200px;
        }
    }
}