.edgtf-header-box{
    .edgtf-top-bar {
        height: 55px;
    }

    .edgtf-page-header {

        .edgtf-menu-area {
            height: 90px;
            box-sizing: border-box;

            .edgtf-logo-wrapper a {
                max-height: 80px; //100*0.9
            }

            .edgtf-grid .edgtf-vertical-align-containers {
                box-sizing: initial;
                margin: 0 -35px;
                padding: 0 35px !important;
            }

            .edgtf-position-right-inner {
                height: 100%;
            }
        }
    }

    .edgtf-drop-down .wide .second > .inner > ul {
        width: 1170px; //1100 + 70, 70 is 35 of padding on each side
    }

    &.edgtf-grid-1000 .edgtf-drop-down .wide .second > .inner > ul {
        width: $grid-width-1000+70;
    }

    &.edgtf-grid-800 .edgtf-drop-down .wide .second > .inner > ul {
        width: $grid-width-800+70;
    }

}
@media only screen and (min-width: 1400px) {
	.edgtf-header-box.edgtf-grid-1300 .edgtf-drop-down .wide .second > .inner > ul {
		width: $grid-width-1300+70;
	}
}
@media only screen and (min-width: 1300px) {
	.edgtf-header-box.edgtf-grid-1200 .edgtf-drop-down .wide .second > .inner > ul {
		width: $grid-width-1200+70;
	}
}

