@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.edgtf-comment {
		
		&.edgtf-pingback-comment {
			
			.edgtf-comment-text {
				padding: 0;
			}
		}
	}

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .edgtf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 6px 0 7px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .edgtf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .edgtf-comment-form-inner {
        padding: 82px 0 108px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0 0 0 60px;
            }
        }
    }
}

@include phone-landscape {
	
    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {
	        
            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
    width: 100%;
    margin: 0 0 15px;
    padding: 16px 24px 15px;
    font-family: inherit;
    font-size: 12px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include edgtfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }
    
    @include edgtfPlaceholder {
        color: $default-text-color;
    }
}

#respond textarea,
#respond input[type='text'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

#respond textarea {
    display: block;
    padding: 15px 24px;
	margin: 0 0 17px;
}

p.form-submit {
    margin: 0;
}

#submit_comment,
.post-password-form input[type='submit'] {
    @include edgtfButtonDefaultStyle();
    @include edgtfButtonMediumParams();
    @include edgtfButtonSolidColor();

    &:hover {
        background-color: $default-heading-color;
    }
}

%cf7-input-style {
    width: 100%;
    margin: 0 0 15px;
    padding: 10px 20px;
    font-family: inherit;
    font-size: 12px;
    line-height: 22px;
    font-weight: inherit;
    color: $default-text-color;
    background-color: #fff;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;

    &:focus {
        color: $default-text-color;
        background-color: #f7f7f7;
    }
	
	@include edgtfPlaceholder {
		color: $default-text-color;
	}
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %cf7-input-style;

        footer & {
            margin: 0;
        }
    }
}

input.wpcf7-form-control.wpcf7-submit {
    @include edgtfButtonDefaultStyle();
    @include edgtfButtonMediumParams();
    @include edgtfButtonSolidColor();

    &:hover {
        background-color: $default-heading-color;
		cursor: pointer;
    }
}

.wpcf7-form {
	
	p {
		margin: 0;
	}
	
	textarea {
		vertical-align: middle;
	}
	
	.ajax-loader {
		position: absolute;
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
  #Single Image styles - end
  ========================================================================== */   

a.vc_single_image-wrapper {
   position:relative;

    &:after {
        content:'';
        position:absolute;
        top:0;
        left:0;
        height:100%;
        width:100%;
        opacity:0;
        background-color: rgba(0,0,0,.15);
        @include edgtfTransition(opacity .4s);
    }

    &:hover {
        &:after {
            opacity: 1;
        }
    }
}

/* ==========================================================================
 #Singe Image styles - end
 ========================================================================== */   

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
    
    &.edgtf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 19px); // 19 is is half height of pagination control
                @include edgtfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			@include edgtfTransform(translateY(-50%));
			
			&:hover {
				
				.edgtf-prev-icon,
				.edgtf-next-icon {
					color: $first-main-color;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
		
		.edgtf-prev-icon,
		.edgtf-next-icon {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			color: $default-heading-color;
			line-height: 1;
			@include edgtfTransition(color .2s ease-out);
			
			.edgtf-icon-arrow {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 40px;
				line-height: inherit;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 28px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 8px;
				height: 8px;
				margin: 0 7px;
				border-radius: 3em;
				background-color: rgba($default-heading-color, .3);
				@include edgtfTransition(background-color .2s ease-out);
				box-sizing: border-box;
			}
			
			&.active {
				
				span {
					width: 10px;
					height: 10px;
					background-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.edgtf-404-page {

    &.edgtf-wrapper {
        padding: 0 !important;
    }
    
    .edgtf-content {
        height: 100vh;
    }

    .edgtf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .edgtf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 14%;

        .edgtf-page-not-found-title {
			font-size: 60px;
            margin: 0;
        }

	    .edgtf-page-not-found-text {
		    color: #999;
			font-size: 18px;
		    margin: 34px 0 0;
	    }

        .edgtf-btn {
            margin: 51px 0 0;
	
	        &.edgtf-btn-light {
		        
		        &:hover {
			        color: $default-heading-color !important;
			        background-color: #fff !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }

	@media only screen and (min-width: $laptop-landscape-medium-plus-pixel) {

		.edgtf-404-content-wrap {
			width: 50%;
		}
	}

    @include ipad-portrait {

        .edgtf-page-not-found {
            padding: 40px 30px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgtfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        @include edgtfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include edgtfTransition(.15s ease .05s);
            font-size: 15px;
        }

        &:after {
            content: '';
            position: absolute;
            z-index: 1;
            top: 12px;
            left: 19px;
            height: 0;
            width: 0;
            opacity: 0;
            border: 1px solid $default-border-color;
            @include edgtfTransition(0.2s cubic-bezier(0.54, 0.13, 0, 1.43));
        }

        &:hover {
            background-color: $default-heading-color;
            border: 1px solid $default-heading-color;

            &:after {
                height: 12px;
                opacity: 1;
                @include edgtfTransition(0.45s cubic-bezier(0.54, 0.13, 0, 1) .05s);
            }

            i {
                @include edgtfTransform(translateY(-7px));
                @include edgtfTransition(.2s cubic-bezier(1,-1.21, 0.25, 1));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
    .edgtf-video-wrap {
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */